import { createContext, useContext } from "react";
import SafeBeesQueen from "../../contracts/SafeBeesQueen";

import { SafeBeesQueenAccount, SafeBeesQueenStats } from "./interfaces";

export interface ISafeBeesQueenContext {
  account: SafeBeesQueenAccount;
  contract?: SafeBeesQueen | null;
  referrerAddress?: string;
  stats: SafeBeesQueenStats;
}

const SafeBeesQueenContext = createContext<ISafeBeesQueenContext>({
  account: {
    balance: 0,
    referrer: "",
    referralsCount: 0,
    loading: true,
  },
  stats: {
    loading: true,
    totalSupply: 0,
    burnedPercent: 0,
    burned: 0,
    locked: 0,
    lockedPercent: 0,
    liqudity: 0,
    liqudityPercent: 0,
    usdPrice: 0,
    symbol: "",
    owner: "",
  },
});

export default SafeBeesQueenContext;

export const useSafeBeesQueen = (): ISafeBeesQueenContext => {
  return useContext<ISafeBeesQueenContext>(SafeBeesQueenContext);
};
