import React from "react";

import "./FormGroup.css";

interface FormGroupProps {
  label: string;
  help?: any;
}

const FormGroup: React.FC<FormGroupProps> = ({ label, children, help }) => {
  return (
    <div className="form-group">
      {label && <div className="form-group__label">{label}</div>}
      <div className="form-group__input-wrapper">{children}</div>
      {help && <div className="form-group__help">{help}</div>}
    </div>
  );
};

export default FormGroup;
