import React from "react";

import "./AmountInput.css";

export interface AmountInputProps {
  value: string;
  onChange?: (value: string) => void;
  onFocus?: () => void;
  max?: string;
  currency: string;
  disabled?: boolean;
  isInvalid?: boolean;
  loading?: boolean;
  errorText?: string;
  decimalsLength?: number;
}

const AmountInput: React.FC<AmountInputProps> = ({
  value,
  onChange,
  onFocus,
  currency,
  max,
  disabled,
  isInvalid,
  loading,
  errorText,
  decimalsLength,
}) => {
  const handleChagne = (e: React.SyntheticEvent<HTMLInputElement>) => {
    const { value } = e.target as HTMLInputElement;

    if (decimalsLength) {
      const splitedValue = value.split(".");

      if (splitedValue.length > 1 && splitedValue[1].length > 2) return;
    }

    const formattedValue = value
      .replace(/[^0-9.]/g, "")
      .replace(/(\..*)\./g, "$1");

    if (onChange) onChange(formattedValue);
  };

  const maxNumber = parseFloat(max || "0.0");

  const onSetMax = () => {
    if (max && maxNumber > 0 && !disabled && onChange) {
      onChange(max);
    }
  };

  currency = currency || "";

  return (
    <div className="amount-input__wrapper">
      <input
        type="text"
        value={value}
        onChange={handleChagne}
        onFocus={onFocus}
        placeholder="0.00"
        className="amount-input"
        disabled={disabled || loading}
      />
      <div className="amount-input__currency">{currency.slice(0, 10)}</div>
      {maxNumber > 0 && (
        <div className="amount-input__max" onClick={onSetMax}>
          Max
        </div>
      )}
      {isInvalid && errorText ? (
        <div className="amount-input__error-text">{errorText}</div>
      ) : null}
    </div>
  );
};

export default AmountInput;
