import React, { useState } from "react";
import { CopyToClipboard } from "react-copy-to-clipboard";
import { useWeb3React } from "@web3-react/core";
import { Row, Col, Container } from "react-bootstrap";

import Card from "../../components/Card";
import Section from "../../components/Section";

import "./TokenReferralsSection.css";
import { FaCopy } from "react-icons/fa";
import { useSafeBeesQueen } from "../../providers/SafeBeesQueenProvider/SafeBeesQueenContext";
import { ReferralLevel } from "../../components/ReferralLevels/ReferralLevels";

const TokenReferralsSection = () => {
  const { account: address } = useWeb3React();
  const { account, stats } = useSafeBeesQueen();
  const [copied, setCopied] = useState<boolean>(false);
  const link = `${window.location.origin}?ref=${address || "..."}`;
  const onCopy = () => {
    setCopied(true);
    setTimeout(() => {
      setCopied(false);
    }, 3000);
  };
  const showRefLink = account.balance > 0 || address === stats.owner;
  return (
    <Section className="token-referrals-section">
      <Card title="Referral System">
        <Row>
          <Col xs={12}>
            {showRefLink ? (
              <>
                <div className="token-referrals__link-wrapper">
                  <div className="token-referrals__link">{link}</div>
                  {copied ? (
                    <div>Copied</div>
                  ) : (
                    <CopyToClipboard text={link} onCopy={onCopy}>
                      <FaCopy className="token-referrals__link-copy" />
                    </CopyToClipboard>
                  )}
                </div>
              </>
            ) : (
              <>
                <div className="token-referrals__link-wrapper">
                  Buy {stats.symbol} to get a referral link.
                </div>
              </>
            )}
            <p className="token-referrals__caption">
              Earn passively 3% of the {stats.symbol} transactions from anyone
              who submitted you as referrer forever.
            </p>
          </Col>
          <Col xs={12}>
            <ReferralLevel
              title="Your referrals"
              level={1}
              count={account.referralsCount}
            />
          </Col>
        </Row>
      </Card>
    </Section>
  );
};

export default TokenReferralsSection;
