import React from "react";

import Button from "../../components/Button";
import ConnectWallet from "../../components/ConnectWallet";
import { useSafeBeesQueen } from "../../providers/SafeBeesQueenProvider/SafeBeesQueenContext";

const SwapButton: React.FC<any> = ({ account, canSwap, loading, onBuy }) => {
  const { stats } = useSafeBeesQueen();
  if (!account) {
    return <ConnectWallet />;
  }
  return (
    <Button
      type="secondary"
      disabled={!canSwap}
      loading={loading}
      onClick={onBuy}
    >
      Buy {stats.symbol}
    </Button>
  );
};

export default SwapButton;
