import React, { useState } from "react";

import Card from "../../components/Card";
import Section from "../../components/Section";
import { fromWeiWithDecimals, numberWithCommas, roundTo } from "../../utils";

import "./TokenInfoSection.css";
import Loader from "../../components/Loader";
import { useSafeBeesQueen } from "../../providers/SafeBeesQueenProvider/SafeBeesQueenContext";
import CopyToClipboard from "react-copy-to-clipboard";
import { getToken } from "../../config";
import useActiveWeb3React from "../../hooks/useActiveWeb3React";
import ChainId from "../../constants/ChainId";
import { FaCopy } from "react-icons/fa";
import AddToMetamask from "../../components/AddToMetamask";

const InfoSectionItem: React.FC<any> = ({ label, children, loading }) => {
  return (
    <div className="token-info-section__item">
      <div className="token-info-section__item-value">
        {loading ? <Loader /> : children}
      </div>
      <div className="token-info-section__item-label">{label}</div>
    </div>
  );
};

const InfoSection: React.FC = () => {
  const { stats } = useSafeBeesQueen();
  const { chainId } = useActiveWeb3React();
  const [copied, setCopied] = useState<boolean>(false);
  const {
    totalSupply,
    burnedPercent,
    usdPrice,
    symbol,
    lockedPercent,
    liqudityPercent,
  } = stats;
  const token = getToken(chainId as ChainId) || "";
  const tokenLabel = token.slice(0, 5) + "..." + token.slice(-5);

  const circulatingSupply =
    100 - burnedPercent - lockedPercent - liqudityPercent;

  const onCopy = () => {
    setCopied(true);
    setTimeout(() => {
      setCopied(false);
    }, 3000);
  };
  return (
    <Section className="token-info-section" small>
      <Card title="Stats">
        <div className="token-info__address">
          {tokenLabel}{" "}
          {copied ? (
            <div>Copied</div>
          ) : (
            <CopyToClipboard text={token} onCopy={onCopy}>
              <FaCopy />
            </CopyToClipboard>
          )}
        </div>
        <AddToMetamask address={token} symbol={symbol} />
        <div>
          <InfoSectionItem label="Price" loading={stats.loading}>
            ${roundTo(usdPrice, 11).toFixed(11)}
          </InfoSectionItem>
        </div>
        <div>
          <InfoSectionItem label="Total Supply" loading={stats.loading}>
            {numberWithCommas(roundTo(totalSupply, 2))} {symbol}
          </InfoSectionItem>
        </div>
        <div className="token-info-section__items">
          <InfoSectionItem label="Burned" loading={stats.loading}>
            {roundTo(burnedPercent, 2)} %
          </InfoSectionItem>
          <InfoSectionItem label="Locked" loading={stats.loading}>
            {roundTo(lockedPercent, 2)} %
          </InfoSectionItem>
          <InfoSectionItem label="Liqudity" loading={stats.loading}>
            {roundTo(liqudityPercent, 2)} %
          </InfoSectionItem>
          <InfoSectionItem label="Circulating" loading={stats.loading}>
            {roundTo(circulatingSupply, 2)} %
          </InfoSectionItem>
        </div>
      </Card>
    </Section>
  );
};

export default InfoSection;
