import React from "react";
import { BrowserRouter as Router } from "react-router-dom";
import { Web3ReactProvider } from "@web3-react/core";
import {
  ExternalProvider,
  JsonRpcFetchFunc,
  Web3Provider,
} from "@ethersproject/providers";
import { Container, Row, Col } from "react-bootstrap";
import { ToastContainer } from "react-toastify";

import Header from "./components/Header";

import "./App.css";
import ReferralSystemSection from "./sections/ReferralSystemSection";
import ReHireSection from "./sections/ReHireSection/ReHireSection";
import InfoSection from "./sections/InfoSection/InfoSection";
import HireSection from "./sections/HireSection/HireSection";
import FAQSection from "./sections/FAQSection/FAQSection";
import Footer from "./components/Footer";
import Web3ProviderNetwork from "./providers/Web3ProviderNetwork";
import BNBHiveProvider from "./providers/BNBHiveProvider/BNBHiveProvider";
import RefreshContextProvider from "./providers/RefreshProvider";
import Web3ReactManager from "./components/Web3ReactManager";
import NFTAnnounceSection from "./sections/NFTAnnounceSection";
import TokenCountdownSection from "./sections/TokenCountdownSection";
import VideoSection from "./sections/VideoSection";
import Section from "./components/Section";
import Card from "./components/Card";
import SafeBeesQueenProvider from "./providers/SafeBeesQueenProvider";
import TokenSection from "./sections/TokenSection";
import TokenInfoSection from "./sections/TokenInfoSection";
import TokenReferralsSection from "./sections/TokenReferralsSection";
import VestingSection from "./sections/VestingSection";

function getLibrary(provider: ExternalProvider | JsonRpcFetchFunc): any {
  return provider;
  // const library = new Web3Provider(provider);
  // library.pollingInterval = 8000;
  // return library;
}

function App() {
  return (
    <Router>
      <Web3ReactProvider getLibrary={getLibrary}>
        <Web3ProviderNetwork getLibrary={getLibrary}>
          <Web3ReactManager>
            <RefreshContextProvider>
              <SafeBeesQueenProvider>
                <BNBHiveProvider>
                  <div className="app">
                    <Header />
                    <div>
                      <Container>
                        <Row>
                          <Col xs={12} md={6}>
                            <TokenSection />
                          </Col>
                          <Col xs={12} md={6}>
                            <TokenInfoSection />
                          </Col>
                          <Col xs={12}>
                            <VestingSection />
                          </Col>
                          <Col xs={12}>
                            <TokenReferralsSection />
                          </Col>
                          <Col xs={12}>
                            <NFTAnnounceSection />
                          </Col>
                        </Row>
                      </Container>
                    </div>
                    <Footer />
                    <ToastContainer />
                  </div>
                </BNBHiveProvider>
              </SafeBeesQueenProvider>
            </RefreshContextProvider>
          </Web3ReactManager>
        </Web3ProviderNetwork>
      </Web3ReactProvider>
    </Router>
  );
}

export default App;
