import { AbiItem } from "web3-utils";
import { fromWeiWithDecimals, roundTo } from "../../utils";
import EthContract from "../EthContract";

import Web3Provider from "../utils/Web3Provider";

import ABI from "./abi/erc20.json";

class ERC20 extends EthContract {
  constructor(address: string, web3Provider: Web3Provider) {
    super(address, ABI as AbiItem[], web3Provider);
  }

  getBalanceOf = (address: string) => this.getVariable("balanceOf", address);
}

export default ERC20;
