import { AbiItem } from "web3-utils";
import { fromWeiWithDecimals } from "../../utils";
import EthContract from "../EthContract";

import Web3Provider from "../utils/Web3Provider";

import ABI from "./abi/safe-bees-queen-vesting.json";

interface Lock {
  amount: number;
  token: string;
  owner: string;
  unlockTime: number;
}

class SafeBeesQueenVesting extends EthContract {
  constructor(address: string, web3Provider: Web3Provider) {
    super(address, ABI as AbiItem[], web3Provider);
  }

  getLock = async (lockId: number): Promise<Lock> => {
    const lock = await this.getVariable("tokenLocks", lockId);
    if (lock) {
      return {
        ...lock,
        amount: parseFloat(fromWeiWithDecimals(lock.amount)),
        unlockTime: parseInt(lock.unlockTime) * 1000,
      };
    }

    return {
      amount: 0,
      token: "",
      owner: "",
      unlockTime: 0,
    };
  };

  getUserLockAt = async (address: string, index: number): Promise<number> => {
    const lockId = await this.getVariable<number>("userLockAt", address, index);
    return lockId === null ? -1 : lockId;
  };

  getUserLocksLength = async (address: string): Promise<number> => {
    const length = await this.getVariable<number>("userLocksLength", address);

    return length || 0;
  };

  getUserLocks = async (address: string): Promise<Lock[]> => {
    const length = await this.getUserLocksLength(address);
    const locks = [];

    for (let i = 0; i < length; i++) {
      const lockId = await this.getUserLockAt(address, i);
      const lock = await this.getLock(lockId);
      locks.push(lock);
    }

    return locks;
  };

  getAccountInfo = async (address: string): Promise<any> => {
    const locks = await this.getUserLocks(address);
    return {
      locks,
    };
  };
}

export default SafeBeesQueenVesting;
