import React from "react";
import Countdown from "react-countdown";

import Card from "../../components/Card";
import Section from "../../components/Section";
import { ZERO_ADDRESS } from "../../constants";
import SetReferrerForm from "../../forms/SetReferrerForm";
import SwapForm from "../../forms/SwapForm";
import useActiveWeb3React from "../../hooks/useActiveWeb3React";

import { useSafeBeesQueen } from "../../providers/SafeBeesQueenProvider/SafeBeesQueenContext";

import "./TokenSection.css";

const TokenCountdownSection = () => {
  const { account } = useActiveWeb3React();
  const { account: tokenAccount, stats } = useSafeBeesQueen();
  const hasReferrer =
    tokenAccount.referrer !== ZERO_ADDRESS || stats.owner === account;
  return (
    <Section>
      <Card title="Safe Bees Queen">
        {hasReferrer ? <SwapForm /> : <SetReferrerForm />}
      </Card>
    </Section>
  );
};

export default TokenCountdownSection;
