import React, { useState, useMemo, useEffect } from "react";
import config from "../../config";
import ChainId from "../../constants/ChainId";

import SafeBeesQueenVesting from "../../contracts/SafeBeesQueenVesting";
import Web3Provider from "../../contracts/utils/Web3Provider";

import useActiveWeb3React from "../../hooks/useActiveWeb3React";
import useReferrer from "../../hooks/useReferrer";
import useRefresh from "../../hooks/useRefresh";
import { RPC } from "../../lib/connectors";

import SafeBeesQueenVestingContext from "./SafeBeesQueenVestingContext";
import { SafeBeesQueenVestingAccount } from "./interfaces";

const SafeBeesQueenVestingProvider: React.FC<any> = ({ children }) => {
  const { chainId, library, account: accountAddress } = useActiveWeb3React();
  // console.log('ChainId', chainId, accountAddress, library)
  const { fastRefresh } = useRefresh();

  const contractAddress = useMemo(
    () => config.tokenVestingContract[chainId as ChainId],
    [chainId]
  );

  const safeBeesQueenVestingContract = useMemo(() => {
    if (!chainId) return null;
    let provider = new Web3Provider(
      RPC[chainId as ChainId],
      chainId as ChainId
    );
    if (library) {
      provider = new Web3Provider(library, chainId as ChainId);
    }
    const contract = new SafeBeesQueenVesting(contractAddress, provider);
    return contract;
  }, [chainId, contractAddress, library]);

  const [account, setAccount] = useState<SafeBeesQueenVestingAccount>({
    locks: [],
    loading: true,
  });

  useEffect(() => {
    const fetchAccount = async () => {
      try {
        if (accountAddress && safeBeesQueenVestingContract) {
          const account: SafeBeesQueenVestingAccount =
            await safeBeesQueenVestingContract.getAccountInfo(accountAddress);
          setAccount({
            ...account,
            loading: false,
          });
        } else {
          setAccount((prevState: SafeBeesQueenVestingAccount) => ({
            ...prevState,
            loading: false,
          }));
        }
      } catch (e) {
        console.error(e);
      }
    };

    fetchAccount();
  }, [safeBeesQueenVestingContract, accountAddress, fastRefresh]);

  const value = {
    account,
    contract: safeBeesQueenVestingContract,
  };
  return (
    <SafeBeesQueenVestingContext.Provider value={value}>
      {children}
    </SafeBeesQueenVestingContext.Provider>
  );
};

export default SafeBeesQueenVestingProvider;
