import { AbiItem } from "web3-utils";

import RouterABI from "./abi/swap-router.json";

import EthContract from "../EthContract";
import Web3Provider from "../utils/Web3Provider";
import { fromWeiWithDecimals, toWeiWithDecimals } from "../../utils";
import ChainId from "../../constants/ChainId";
import config from "../../config";

class SwapRouterContract extends EthContract {
  constructor(address: string, web3Provider: Web3Provider) {
    super(address, RouterABI as AbiItem[], web3Provider);
  }

  swapTokens = async (
    address0: string,
    address1: string,
    amount: string,
    accountAddress: string,
    slippage: number = 0.2
  ) => {
    const tokens = [address0, address1];
    const time = Math.floor(Date.now() / 1000) + 200000;
    const wbnbAddress = config.wbnbContract[this.web3Provider.networkId];
    const deadline = toWeiWithDecimals(time.toString());
    const amountIn = toWeiWithDecimals(amount);

    if (address0 === wbnbAddress) {
      // Eth -> Token
      const amountsOut = await this.getAmountsOut(amountIn, tokens);
      const buyAmount = parseFloat(fromWeiWithDecimals(amountsOut[1]));
      const minBuyAmount = buyAmount * (1 - slippage);
      return this.swapExactETHForTokensSupportingFeeOnTransferTokens(
        toWeiWithDecimals(minBuyAmount.toString()),
        amountIn,
        tokens,
        deadline,
        accountAddress
      );
    }
    if (address1 === wbnbAddress) {
      // Token -> Eth
      // return this.swapExactTokensForETH(
      //     amountIn,
      //     amountOut[1],
      //     tokens,
      //     accountAddress,
      //     deadline
      // );
      return null;
    }
    const pathWithBNB = [tokens[0], wbnbAddress, tokens[1]];
    const amountOut = await this.getAmountsOut(amountIn, pathWithBNB);
    return this.swapExactTokensForTokens(
      amountIn,
      amountOut[amountOut.length - 1],
      pathWithBNB,
      accountAddress,
      deadline
    );
  };

  getAmountsIn = (amountOut: string, tokens: string[]) =>
    this.contract.methods.getAmountsIn(amountOut, tokens).call();

  getAmountsOut = (amountIn: string, tokens: string[]) =>
    this.contract.methods.getAmountsOut(amountIn, tokens).call();

  swapExactETHForTokens = (
    amountOut: string,
    amountIn: string,
    tokens: string[],
    deadline: string,
    accountAddress: string
  ) =>
    this.contract.methods
      .swapExactETHForTokens(amountOut, tokens, accountAddress, deadline)
      .send({
        value: amountIn,
        from: accountAddress,
      });

  swapExactETHForTokensSupportingFeeOnTransferTokens = async (
    amountOutMin: string,
    amountIn: string,
    path: string[],
    deadline: string,
    accountAddress: string
  ) => {
    const method =
      this.contract.methods.swapExactETHForTokensSupportingFeeOnTransferTokens;

    const args = [amountOutMin, path, accountAddress, deadline];

    const gas = await method(...args).estimateGas({
      value: amountIn,
      from: accountAddress,
    });

    return method(...args).send({
      value: amountIn,
      from: accountAddress,
    });
  };

  swapExactTokensForTokens = (
    amountIn: string,
    amountOut: string,
    tokens: string[],
    accountAddress: string,
    deadline: string
  ) =>
    this.contract.methods
      .swapExactTokensForTokens(
        amountIn,
        amountOut,
        tokens,
        accountAddress,
        deadline
      )
      .send({
        from: accountAddress,
      });

  addLiquidity = async (
    address0: string,
    address1: string,
    amount0Desired: string,
    amount1Desired: string,
    accountAddress: string,
    chainId: ChainId
  ) => {
    const amount0 = toWeiWithDecimals(amount0Desired);
    const amount1 = toWeiWithDecimals(amount1Desired);

    const amount0Min = toWeiWithDecimals((+amount0Desired * 0.75).toString());
    const amount1Min = toWeiWithDecimals((+amount1Desired * 0.75).toString());

    const time = Math.floor(Date.now() / 1000) + 200000;
    const deadline = time.toString();

    const wbnbAddress = config.wbnbContract[this.web3Provider.networkId];

    if (address0 === wbnbAddress || address1 === wbnbAddress) {
      const addressToken = address0 === wbnbAddress ? address1 : address0;
      const amountToken = address0 === wbnbAddress ? amount1 : amount0;
      const amountMinToken = address0 === wbnbAddress ? amount1Min : amount0Min;
      const amountMinETH = address0 === wbnbAddress ? amount0Min : amount1Min;
      await this.contract.methods
        .addLiquidityETH(
          addressToken,
          amountToken,
          amountMinToken,
          amountMinETH,
          accountAddress,
          deadline
        )
        .send({
          value: amountToken,
          from: accountAddress,
        });
    } else {
      await this.contract.methods
        .addLiquidity(
          address0,
          address1,
          amount0,
          amount1,
          amount0Min,
          amount1Min,
          accountAddress,
          deadline
        )
        .send({
          from: accountAddress,
        });
    }
  };
}

export default SwapRouterContract;
