import React from "react";

import { SWAP } from "../../config";

import { CURRENCY } from "../../interfaces/Currency";

import SwapProvider from "../../providers/SwapProvider";

import useSwapForm from "../../hooks/useSwapForm";

import { numberWithCommas, roundTo } from "../../utils";

import SwapButton from "./SwapButton";
import SwapRatios from "./SwapRatios";
import AmountInput from "../../components/AmountInput";
import { useSafeBeesQueen } from "../../providers/SafeBeesQueenProvider/SafeBeesQueenContext";
import FormGroup from "../../components/FormGroup";

import "./SwapForm.css";
import ErrorText from "../../components/ErrorText";

const SwapInputs = () => {
  const { stats } = useSafeBeesQueen();
  const {
    chainId,
    form,
    fromMaxBalance,
    account,
    bnbBalance,
    tokenBalance,
    canSwap,
    loading,
    onChange,
    onBuy,
    error,
  } = useSwapForm();

  return (
    <>
      <div>
        <FormGroup
          label="How much BNB you need to pay:"
          help={`${numberWithCommas(roundTo(bnbBalance, 5))} ${CURRENCY.BNB}`}
        >
          <AmountInput
            onChange={onChange("bnbAmount")}
            value={form.bnbAmount}
            currency={CURRENCY.BNB}
            max={fromMaxBalance}
          />
        </FormGroup>
        <FormGroup
          label={`How much ${stats.symbol} you want to buy:`}
          help={`${numberWithCommas(tokenBalance)} ${stats.symbol}`}
        >
          <AmountInput
            onChange={onChange("tokenAmount")}
            value={form.tokenAmount}
            currency={stats.symbol}
          />
        </FormGroup>
      </div>
      <SwapRatios fromAmount={form.bnbAmount} />
      <div>
        <SwapButton
          account={account}
          canSwap={canSwap}
          loading={loading}
          onBuy={onBuy}
          chainId={chainId}
        />
        {error && <ErrorText>{error.message}</ErrorText>}
      </div>
    </>
  );
};

interface SwapFormProps {
  cardTitleAs?: string;
}

const SwapForm: React.FC<SwapFormProps> = ({ cardTitleAs }) => (
  <div className="swap-form">
    <SwapProvider swapKey={SWAP.PANCAKE_V2}>
      <SwapInputs />
    </SwapProvider>
  </div>
);

export default SwapForm;
