import { useState } from "react";
import ChainId from "../constants/ChainId";
import { useSafeBeesQueen } from "../providers/SafeBeesQueenProvider/SafeBeesQueenContext";

import { useSwap } from "../providers/SwapProvider/SwapProvider";
import { fromWeiWithDecimals, toWeiWithDecimals } from "../utils";

import config from "../config";

import useActiveWeb3React from "./useActiveWeb3React";

const getWbnb = (chainId: ChainId) => config.wbnbContract[chainId];
const getToken = (chainId: ChainId) => config.tokenContract[chainId];

const useSwapForm = () => {
  const { account: tokenAccount } = useSafeBeesQueen();
  const { account, balance, chainId } = useActiveWeb3React();
  const [slippage, setSlippage] = useState<number>(0.2);
  const [error, setError] = useState<any>(null);
  const bnbBalance = fromWeiWithDecimals(balance);
  const swap = useSwap();

  const tokenBalance = tokenAccount.balance;

  const [loading, setLoading] = useState<boolean>(false);
  const [loadingRatio, setLoadingRatio] = useState<boolean>(false);

  const [form, setForm] = useState<any>({
    bnbAmount: bnbBalance,
    tokenAmount: "0",
  });

  const handleChange = (value: any) => {
    setForm((prevValue: any) => ({ ...prevValue, ...value }));
  };

  const convertValues = async (fromValue: any, fromName: any) => {
    const fromAmount = toWeiWithDecimals(
      (fromValue * (1 - slippage)).toString()
    );
    const tokens = [];
    let toAmount;
    // We support swapping on in Binance Smart Chain
    if (fromName === "bnbAmount") {
      tokens.push(getWbnb(chainId as ChainId));
      tokens.push(getToken(chainId as ChainId));
      toAmount = "tokenAmount";
    } else {
      tokens.push(getToken(chainId as ChainId));
      tokens.push(getWbnb(chainId as ChainId));
      toAmount = "bnbAmount";
    }
    setLoadingRatio(true);
    setError(null);
    try {
      if (swap && swap.router && fromValue > 0) {
        const amountsOut = await swap.router.getAmountsOut(fromAmount, tokens);
        const outputValue = fromWeiWithDecimals(
          amountsOut[amountsOut.length - 1]
        );
        const output = {
          [fromName]: fromValue,
          [toAmount]: outputValue,
        };
        return output;
      } else {
        return {
          [fromName]: fromValue,
          [toAmount]: "0",
        };
      }
    } catch (e) {
      console.error(e);
      const output = {
        [fromName]: fromValue,
        [toAmount]: "0",
      };
      return output;
    } finally {
      setLoadingRatio(false);
    }
  };

  const onChange = (name: string) => (value: any) => {
    convertValues(value, name).then((output) => {
      handleChange(output);
    });
  };

  const fromMaxBalance = Math.max(parseFloat(bnbBalance) - 0.002, 0).toString();

  const onBuy = async () => {
    setLoading(true);
    try {
      setError(null);
      if (swap && swap.router && account) {
        const wbnbAddress = getWbnb(chainId as ChainId);
        const tokenAddress = getToken(chainId as ChainId);
        await swap.router.swapTokens(
          wbnbAddress,
          tokenAddress,
          form.bnbAmount,
          account
        );

        setForm({
          bnbAmount: "0",
          tokenAmount: "0",
        });
      }
    } catch (e) {
      console.error(e);
      setError(e);
    }
    setLoading(false);
  };

  const canSwap =
    parseFloat(form.bnbAmount) > 0 &&
    parseFloat(form.bnbAmount) <= parseFloat(fromMaxBalance);

  return {
    chainId,
    form,
    fromMaxBalance,
    account,
    bnbBalance,
    tokenBalance,
    canSwap,
    loadingRatio,
    loading,
    onChange,
    onBuy,
    error,
  };
};

export default useSwapForm;
