import React, { useCallback } from "react";
import { Tooltip } from "react-bootstrap";
import useActiveWeb3React from "../../hooks/useActiveWeb3React";

import "./AddToMetamask.css";

interface AddToMetamaskProps {
  address: string;
  symbol: string;
  image?: string;
  decimals?: number;
}

const AddToMetamask: React.FC<AddToMetamaskProps> = ({
  address,
  symbol,
  image,
  decimals = 18,
}) => {
  const { library } = useActiveWeb3React();
  const addWatchSpaceToken = useCallback(async () => {
    // eslint-disable-next-line @typescript-eslint/ban-ts-comment
    // @ts-ignore
    const provider = window.ethereum;
    if (provider) {
      try {
        // wasAdded is a boolean. Like any RPC method, an error may be thrown.
        const wasAdded = await provider.request({
          method: "wallet_watchAsset",
          params: {
            type: "ERC20",
            options: {
              address,
              symbol,
              image,
              decimals,
            },
          },
        });

        if (wasAdded) {
          console.log("Token was added");
        }
      } catch (error) {
        // TODO: find a way to handle when the user rejects transaction or it fails
      }
    }
  }, [address, symbol, image, decimals]);

  if (!library.isMetaMask) return null;

  return (
    <div className="add-to-metamask" onClick={addWatchSpaceToken}>
      Add {symbol} token to metamask{" "}
      <img src="/images/wallets/metamask.png" alt="metamask" />
    </div>
  );
};

export default AddToMetamask;
