import { createContext, useContext } from "react";
import SafeBeesQueenVesting from "../../contracts/SafeBeesQueenVesting";

import { SafeBeesQueenVestingAccount } from "./interfaces";

export interface ISafeBeesQueenVestingContext {
  account: SafeBeesQueenVestingAccount;
  contract?: SafeBeesQueenVesting | null;
}

const SafeBeesQueenVestingContext = createContext<ISafeBeesQueenVestingContext>(
  {
    account: {
      locks: [],
      loading: true,
    },
  }
);

export default SafeBeesQueenVestingContext;

export const useSafeBeesQueenVesting = (): ISafeBeesQueenVestingContext => {
  return useContext<ISafeBeesQueenVestingContext>(SafeBeesQueenVestingContext);
};
