import React from "react";
import moment from "moment";
import { Col, Row } from "react-bootstrap";

import Card from "../../components/Card";
import Section from "../../components/Section";

import { useSafeBeesQueen } from "../../providers/SafeBeesQueenProvider/SafeBeesQueenContext";
import { useSafeBeesQueenVesting } from "../../providers/SafeBeesQueenVestingProvider/SafeBeesQueenVestingContext";

import SafeBeesQueenVestingProvider from "../../providers/SafeBeesQueenVestingProvider";
import { roundTo, numberWithCommas } from "../../utils";

import "./VestingSection.css";

const VestingCard = () => {
  const { stats } = useSafeBeesQueen();
  const { account } = useSafeBeesQueenVesting();
  if (account.loading || !account.locks.length) return null;

  const totalTokens = account.locks.reduce((acc, lock) => {
    return acc + lock.amount;
  }, 0);

  const totalTokensInUsd = totalTokens * stats.usdPrice;
  return (
    <Card title="Your vested tokens">
      <Row>
        {account.locks.map((lock, index) => {
          const isUnlocked = lock.unlockTime < Date.now();
          return (
            <Col xs={12} md={6}>
              <div className="vesting-item" key={index}>
                <div className="vesting-item__number">{index + 1}</div>
                <div className="vesting-item__content">
                  {!isUnlocked && (
                    <div className="vesting-item__date">
                      Will be unlocked at{" "}
                      {moment(lock.unlockTime, "x").format("HH:mm DD/MM/YYYY")}
                    </div>
                  )}
                  <div className="vesting-item__amount">
                    {numberWithCommas(lock.amount)} {stats.symbol}
                  </div>
                  <div className="vesting-item__amount-usd">
                    $
                    {numberWithCommas(roundTo(lock.amount * stats.usdPrice, 2))}
                  </div>
                </div>
              </div>
            </Col>
          );
        })}
      </Row>
      <div className="vesting-totals">
        <div className="vesting-totals__title">Totals:</div>
        <div className="vesting-totals__token">
          {numberWithCommas(totalTokens)} {stats.symbol}
        </div>
        <div className="vesting-totals__usd">
          ${numberWithCommas(roundTo(totalTokensInUsd))}
        </div>
      </div>
    </Card>
  );
};

const VestingSection = () => {
  return (
    <Section>
      <SafeBeesQueenVestingProvider>
        <VestingCard />
      </SafeBeesQueenVestingProvider>
    </Section>
  );
};

export default VestingSection;
