import React, { useState, useMemo, useEffect } from "react";
import config from "../../config";
import ChainId from "../../constants/ChainId";

import SafeBeesQueen from "../../contracts/SafeBeesQueen";
import Web3Provider from "../../contracts/utils/Web3Provider";

import useActiveWeb3React from "../../hooks/useActiveWeb3React";
import useReferrer from "../../hooks/useReferrer";
import useRefresh from "../../hooks/useRefresh";
import { RPC } from "../../lib/connectors";

import SafeBeesQueenContext from "./SafeBeesQueenContext";
import { SafeBeesQueenAccount, SafeBeesQueenStats } from "./interfaces";

const SafeBeesQueenProvider: React.FC<any> = ({ children }) => {
  const { chainId, library, account: accountAddress } = useActiveWeb3React();
  // console.log('ChainId', chainId, accountAddress, library)
  const referrerAddress = useReferrer();
  const { fastRefresh } = useRefresh();

  const contractAddress = useMemo(
    () => config.tokenContract[chainId as ChainId],
    [chainId]
  );

  const safeBeesQueenContract = useMemo(() => {
    if (!chainId) return null;
    let provider = new Web3Provider(
      RPC[chainId as ChainId],
      chainId as ChainId
    );
    if (library) {
      provider = new Web3Provider(library, chainId as ChainId);
    }
    const contract = new SafeBeesQueen(contractAddress, provider);
    return contract;
  }, [chainId, contractAddress, library]);

  const [account, setAccount] = useState<SafeBeesQueenAccount>({
    balance: 0,
    referrer: "",
    referralsCount: 0,
    loading: true,
  });

  const [stats, setStats] = useState<SafeBeesQueenStats>({
    loading: true,
    totalSupply: 0,
    burnedPercent: 0,
    burned: 0,
    locked: 0,
    lockedPercent: 0,
    liqudity: 0,
    liqudityPercent: 0,
    usdPrice: 0,
    symbol: "",
    owner: "",
  });

  useEffect(() => {
    const fetchStats = async () => {
      try {
        if (safeBeesQueenContract) {
          const stats = await safeBeesQueenContract.getStats();
          setStats({
            ...stats,
            loading: false,
          });
        } else {
          setStats((prevState: SafeBeesQueenStats) => ({
            ...prevState,
            loading: false,
          }));
        }
      } catch (e) {
        console.error(e);
      }
    };
    fetchStats();
  }, [safeBeesQueenContract, fastRefresh]);

  useEffect(() => {
    const fetchAccount = async () => {
      try {
        if (accountAddress && safeBeesQueenContract) {
          const account: SafeBeesQueenAccount =
            await safeBeesQueenContract.getAccountInfo(accountAddress);
          setAccount({
            ...account,
            loading: false,
          });
        } else {
          setAccount((prevState: SafeBeesQueenAccount) => ({
            ...prevState,
            loading: false,
          }));
        }
      } catch (e) {
        console.error(e);
      }
    };

    fetchAccount();
  }, [safeBeesQueenContract, accountAddress, fastRefresh]);

  const value = {
    account,
    stats,
    contract: safeBeesQueenContract,
  };
  return (
    <SafeBeesQueenContext.Provider value={value}>
      {children}
    </SafeBeesQueenContext.Provider>
  );
};

export default SafeBeesQueenProvider;
