import React, { useEffect, useState } from "react";

import { getToken, getWbnb, SWAP, SWAPS } from "../../config";

import { CURRENCY } from "../../interfaces/Currency";

import { useSwap } from "../../providers/SwapProvider/SwapProvider";

import useActiveWeb3React from "../../hooks/useActiveWeb3React";

import SwapRouterContract from "../../contracts/SwapRouter";

import {
  roundTo,
  fromWeiWithDecimals,
  toWeiWithDecimals,
  numberWithCommas,
} from "../../utils";

import Loader from "../../components/Loader";

import { useSafeBeesQueen } from "../../providers/SafeBeesQueenProvider/SafeBeesQueenContext";
import ChainId from "../../constants/ChainId";
import { RPC } from "../../lib/connectors";
import Web3Provider from "../../contracts/utils/Web3Provider";

import "./SwapRatios.css";

const getCustomSwapRouter = (swapKey: SWAP, chainId: ChainId) => {
  const provider = new Web3Provider(RPC[chainId as ChainId], chainId);
  const swapConfig = SWAPS[swapKey];
  const routerAddress = swapConfig.router.address[chainId];
  return new SwapRouterContract(routerAddress, provider);
};

const SwapRatios = ({ fromAmount }: { fromAmount: string }) => {
  const { stats } = useSafeBeesQueen();
  const currency = CURRENCY.BNB;
  const [loadingRatio, setLoadingRatio] = useState<boolean>(false);
  const [ratio, setRatio] = useState(0);
  const swap = useSwap();
  const { chainId } = useActiveWeb3React();

  const swapKey = swap ? swap.swapKey : "";

  useEffect(() => {
    const fetch = async () => {
      const isSupportedChain = [ChainId.BSC, ChainId.BSC_TESTNET].includes(
        chainId as ChainId
      );
      const defaultFromAmount = parseFloat(fromAmount) === 0 ? "1" : fromAmount;
      setLoadingRatio(true);
      const fromAmountWei = toWeiWithDecimals(defaultFromAmount);
      const tokens = [];
      tokens.push(
        getWbnb(isSupportedChain ? (chainId as ChainId) : ChainId.BSC)
      );
      tokens.push(
        getToken(isSupportedChain ? (chainId as ChainId) : ChainId.BSC)
      );
      try {
        if (swap) {
          // Use default swap router from Binance Smart Chain Pancakeswap Version 1 to display ratio
          const router = isSupportedChain
            ? swap.router
            : getCustomSwapRouter(SWAP.PANCAKE_V2, ChainId.BSC);

          if (router) {
            const amountsOut = await router.getAmountsOut(
              fromAmountWei,
              tokens
            );

            const output = fromWeiWithDecimals(
              amountsOut[amountsOut.length - 1]
            );

            const ratioNew = parseFloat(defaultFromAmount) / parseFloat(output);
            setRatio(ratioNew);
          } else {
            console.error("No router");
          }
        } else {
          console.error("No swap");
        }
      } catch (e) {
        console.error("Swap ratio calculation error", e);
      }
      setLoadingRatio(false);
    };

    fetch();
  }, [fromAmount, swap, swapKey, chainId]);
  return (
    <div className="swap-rates">
      {loadingRatio ? (
        <Loader />
      ) : (
        <>
          {/* <div className="swap-rate">
            1 {stats.symbol} = {roundTo(ratio, 5)} {currency}
          </div> */}
          <div className="swap-rate">
            1 {currency} = {ratio ? numberWithCommas(roundTo(1 / ratio, 5)) : 0}{" "}
            {stats.symbol}
          </div>
        </>
      )}
    </div>
  );
};

export default SwapRatios;
