import React, { createContext, useContext, useMemo } from "react";

import { SWAP, SWAPS } from "../../config";

import useActiveWeb3React from "../../hooks/useActiveWeb3React";
import Web3Provider from "../../contracts/utils/Web3Provider";
import { RPC } from "../../lib/connectors";
import ChainId from "../../constants/ChainId";
import SwapRouterContract from "../../contracts/SwapRouter";

type ISwapContext = {
  swapKey?: SWAP;
  config?: any;
  router?: SwapRouterContract;
} | null;

const SwapContext = createContext<ISwapContext>({});

export const useSwap = () => {
  const swap = useContext(SwapContext);
  return swap;
};

const SwapProvider: React.FC<{ swapKey: SWAP }> = ({ children, swapKey }) => {
  const { chainId, library } = useActiveWeb3React();
  const swap = useMemo(() => {
    let provider = new Web3Provider(
      RPC[chainId as ChainId],
      chainId as ChainId
    );

    if (library) {
      provider = new Web3Provider(library, chainId as ChainId);
    }

    const swapConfig: any = SWAPS[swapKey];

    if (!swapConfig) {
      return null;
    }

    const routerAddress = swapConfig.router.address[chainId as ChainId];

    const router = new SwapRouterContract(routerAddress, provider);

    return {
      swapKey,
      config: swapConfig,
      router,
    };
  }, [library, chainId, swapKey]);

  return <SwapContext.Provider value={swap}>{children}</SwapContext.Provider>;
};

export default SwapProvider;
