import { AbiItem } from "web3-utils";
import config from "../../config";
import { fromWeiWithDecimals, roundTo } from "../../utils";
import ERC20 from "../Erc20";
import EthContract from "../EthContract";

import Web3Provider from "../utils/Web3Provider";

import ABI from "./abi/safe-bees-queen.json";

class SafeBeesQueen extends EthContract {
  initialSupply: number = 1000000000000;

  constructor(address: string, web3Provider: Web3Provider) {
    super(address, ABI as AbiItem[], web3Provider);
  }

  // getters

  getTotalSupply = () => this.getVariable<string>("totalSupply");

  getName = () => this.getVariable<string>("name");

  getSymbol = () => this.getVariable<string>("symbol");

  getOwner = () => this.getVariable<string>("owner");

  getBalanceOf = (address: string) =>
    this.getVariable<string>("balanceOf", address);

  getAllowance = (owner: string, spender: string) =>
    this.getVariable("allowance", owner, spender);

  getReferrer = (referral: string) =>
    this.getVariable<string>("referrers", referral);

  getReferralsCount = async (referrer: string): Promise<number> => {
    const res = await this.getVariable<string>("referralsCount", referrer);
    if (res) return parseInt(res);
    return 0;
  };

  getReferrals = async (referrer: string): Promise<string[]> => {
    const count = await this.getReferralsCount(referrer);
    let referrals: string[] = [];
    for (let i = 0; i < count; i++) {
      const ref = await this.getVariable("referrals", i);
      referrals.push(ref);
    }

    return referrals;
  };

  // functions

  approve = (spender: string, amount: string, from: string) =>
    this.contract.methods.approve(spender, amount).send({ from });

  setReferrer = (referrer: string, from: string) =>
    this.contract.methods.setReferrer(referrer).send({ from });

  // helper functions

  getWbnbUsdPrice = async () => {
    const chainId = this.web3Provider.networkId;
    const wbnbBusdLpContractAddress = config.wbnbBusdLpContract[chainId];
    const wbnbContract = new ERC20(
      config.wbnbContract[chainId],
      this.web3Provider
    );
    const busdContract = new ERC20(
      config.busdContract[chainId],
      this.web3Provider
    );

    const tokenBalanceLP = parseFloat(
      fromWeiWithDecimals(
        await wbnbContract.getBalanceOf(wbnbBusdLpContractAddress)
      )
    );

    const quoteTokenBalanceLP = parseFloat(
      fromWeiWithDecimals(
        await busdContract.getBalanceOf(wbnbBusdLpContractAddress)
      )
    );

    return quoteTokenBalanceLP / tokenBalanceLP;
  };

  getUsdPrice = async (): Promise<number> => {
    const chainId = this.web3Provider.networkId;

    const wbnbUsdPrice = await this.getWbnbUsdPrice();

    const tokenWbnbLpContract = config.tokenWbnbLpContract[chainId];
    const wbnbContract = new ERC20(
      config.wbnbContract[chainId],
      this.web3Provider
    );

    const bnbTokenBalanceLP = parseFloat(
      fromWeiWithDecimals(await wbnbContract.getBalanceOf(tokenWbnbLpContract))
    );

    const tokenBalanceLP = parseFloat(
      fromWeiWithDecimals((await this.getBalanceOf(tokenWbnbLpContract)) || "0")
    );

    const bnbToTokenRate = bnbTokenBalanceLP / tokenBalanceLP;

    return wbnbUsdPrice * bnbToTokenRate;
  };

  getLockedSupply = async (): Promise<any> => {
    const vestingContractAddress =
      config.tokenVestingContract[this.web3Provider.networkId];

    const balance = (await this.getBalanceOf(vestingContractAddress)) || "0";

    return roundTo(parseFloat(fromWeiWithDecimals(balance)), 2);
  };

  getLiquiditySupply = async (): Promise<any> => {
    const liqudityPairAddress =
      config.tokenWbnbLpContract[this.web3Provider.networkId];

    const balance = (await this.getBalanceOf(liqudityPairAddress)) || "0";

    return roundTo(parseFloat(fromWeiWithDecimals(balance)), 2);
  };

  getStats = async (): Promise<any> => {
    const totalSupply = roundTo(
      parseFloat(fromWeiWithDecimals((await this.getTotalSupply()) || "0")),
      2
    );
    const burned = totalSupply > 0 ? this.initialSupply - totalSupply : 0;
    const locked = await this.getLockedSupply();
    const liqudity = await this.getLiquiditySupply();
    return {
      totalSupply,
      burned,
      burnedPercent: roundTo((burned / this.initialSupply) * 100, 2),
      locked,
      lockedPercent: roundTo((locked / totalSupply) * 100, 2),
      liqudity,
      liqudityPercent: roundTo((liqudity / totalSupply) * 100, 2),
      usdPrice: await this.getUsdPrice(),
      symbol: await this.getSymbol(),
      owner: await this.getOwner(),
    };
  };

  getAccountInfo = async (address: string): Promise<any> => {
    const balance = roundTo(
      parseFloat(
        fromWeiWithDecimals((await this.getBalanceOf(address)) || "0")
      ),
      2
    );
    const referrer = await this.getReferrer(address);
    const referralsCount = await this.getReferralsCount(address);
    return {
      balance,
      referrer,
      referralsCount,
    };
  };
}

export default SafeBeesQueen;
