import React, { useEffect, useState } from "react";
import { isAddress } from "web3-utils";

import Button from "../../components/Button";
import ConnectWallet from "../../components/ConnectWallet";
import FormGroup from "../../components/FormGroup";
import Input from "../../components/Input";
import { ZERO_ADDRESS } from "../../constants";
import useActiveWeb3React from "../../hooks/useActiveWeb3React";
import useReferrer from "../../hooks/useReferrer";
import { useSafeBeesQueen } from "../../providers/SafeBeesQueenProvider/SafeBeesQueenContext";

import "./SetReferrerForm.css";

const SetReferrerForm = () => {
  const { account } = useActiveWeb3React();
  const { stats, contract: safeBeesQueenContract } = useSafeBeesQueen();
  const referrer = useReferrer();
  const [touched, setTouched] = useState<boolean>(false);
  const [ref, setRef] = useState<string>("");

  useEffect(() => {
    if (!ref && referrer !== ZERO_ADDRESS && !touched && referrer !== ref) {
      setRef(referrer);
    }
  }, [referrer, ref, touched]);

  const [loading, setLoading] = useState<boolean>(false);

  const handleChange = (e: any) => {
    const { value } = e.target;
    setRef(value);
  };

  const onSetReferrer = async () => {
    try {
      setLoading(true);
      if (safeBeesQueenContract && account) {
        await safeBeesQueenContract.setReferrer(ref, account);
      }
    } catch (e: any) {
      console.error(e);
      alert(e.message);
    } finally {
      setLoading(false);
    }
  };

  const onFocus = () => {
    setTouched(true);
  };

  const canSubmit = !!ref && isAddress(ref);
  return (
    <div className="set-referrer-form">
      <p>
        To buy {stats.symbol} token you need to enter your referrer address
        first. Your referrer must have {stats.symbol} tokens on wallet. You
        can't set yourself as a referrer.
      </p>
      <FormGroup label="Enter your referrer address">
        <Input
          value={ref}
          onChange={handleChange}
          placeholder="0x123...789"
          onFocus={onFocus}
        />
      </FormGroup>
      {account ? (
        <Button
          type="primary"
          disabled={!canSubmit}
          onClick={onSetReferrer}
          loading={loading}
        >
          Set referrer
        </Button>
      ) : (
        <ConnectWallet />
      )}
    </div>
  );
};

export default SetReferrerForm;
