import React from "react";

import { useBnbHive } from "../../providers/BNBHiveProvider/BNBHiveContext";
import { roundTo } from "../../utils";
import InfoItem from "../InfoItem";
import Loader from "../Loader";

import "./ReferralLevels.css";

interface ReferralLevelProps {
  level: number;
  count: number;
  income?: number;
  title?: string;
}

export const ReferralLevel: React.FC<ReferralLevelProps> = ({
  level,
  count,
  income,
  title,
}) => {
  return (
    <div className="referral-level">
      <div className="referral-level__count">{count}</div>
      <div className="referral-level__title">{title || `Level ${level}`}</div>
      {income ? (
        <div className="referral-level__income">{roundTo(income, 3)} BNB</div>
      ) : null}
    </div>
  );
};

const ReferralLevels = () => {
  const { account } = useBnbHive();
  let levels = Object.keys(account.referrals).map((level) => parseInt(level));
  if (!levels.length) levels = [1, 2, 3, 4, 5];
  return (
    <div>
      <div className="referral-levels">
        {account.loading ? (
          <Loader />
        ) : (
          levels.map((level) => {
            const { count, income } = account.referrals[level] || {
              count: 0,
              income: 0,
            };
            return (
              <div className="referral-level" key={level}>
                <div className="referral-level__title">Level {level}</div>
                <div className="referral-level__count">{count}</div>
                <div className="referral-level__income">
                  {roundTo(income, 3)} BNB
                </div>
              </div>
            );
          })
        )}
      </div>
      <InfoItem label="Total referrals" loading={account.loading}>
        {account.referralsCount}
      </InfoItem>
      <InfoItem label="Total ref income" loading={account.loading}>
        {account.referralsIncome} BNB
      </InfoItem>
    </div>
  );
};

export default ReferralLevels;
